<template>
    <p-progress-spinner />
</template>

<script>
import { onMounted } from "vue-demi";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";

export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const store = useStore();

        onMounted(() => {
            const { patient, to } = route.query;

            store.dispatch("artCycle/getCyclesDetails", patient).then(() => {
                router.push({ path: to });
            });
        });
    }
};
</script>
